import { useEffect, useState } from 'react';
import { Bar } from 'react-chartjs-2';
import 'chartjs-adapter-moment';
import data from './problem_3_data.json';

const Score = () => {
  const [scoreData, setScoreData] = useState(null);

  useEffect(() => {
    //   scoreData 
    const labels = data.user_score.map((item) => item.product);
    const productScores = data.user_score.map((item) => item.score);

    setScoreData({
      labels: labels,
      datasets: [
        {
          label: 'Scores',
          data: productScores,
          backgroundColor: [
            'rgba(255, 99, 132, 0.2)', // Product A 
            'rgba(54, 162, 235, 0.2)', // Product B 
            'rgba(75, 192, 192, 0.2)', // Product C 
          ],
          borderColor: [
            'rgba(255, 99, 132, 1)', // Product A  
            'rgba(54, 162, 235, 1)', // Product B  
            'rgba(75, 192, 192, 1)', // Product C  
          ],
          borderWidth: 1, //   
        },
      ],
    });
  }, []);

  if (!scoreData) {
    return <div>Loading...</div>;
  }

  const options = {
    maintainAspectRatio: false, //   
    indexAxis: 'y', // y    
    scales: {
      x: {
        beginAtZero: true,
      },
      y: {
        beginAtZero: true,
      },
    },
  };

  return (
    <div className="score">
      <h1> </h1>
      <div className="barChartBox">
        <Bar data={scoreData} options={options} />
      </div>
    </div>
  );
};

export default Score;
